@layer components {
	.overlay-panel {
		&__mask {
			@apply fixed inset-0 h-full w-full bg-black bg-opacity-70 opacity-0 invisible cursor-pointer z-50 transition-all ease-default duration-500;
		}

		&__body {
			@apply fixed top-0 right-0 h-full w-full bg-quinary z-50 lg:w-sidebar transform translate-x-full transition-all ease-default duration-500;

			.overlay-panel--from-left & {
				@apply left-0 -translate-x-full;
			}
		}

		&__content {
			@apply relative overflow-x-hidden overflow-y-auto h-full;
			-ms-overflow-style: none; /* IE and Edge */
			scrollbar-width: none; /* Firefox */
			&::-webkit-scrollbar {
				@apply hidden;
			}
		}

		&__close {
			@apply absolute top-8 right-9 z-40 text-center text-tertiary cursor-pointer opacity-0 invisible transform transition-all -translate-y-3 ease-default duration-500 delay-500;
		}

		&.is-overlay-panel-open {
			.overlay-panel__mask {
				@apply opacity-100 visible;
			}
			.overlay-panel__body {
				@apply transform-none;
			}
			.overlay-panel__close {
				@apply opacity-100 visible transform-none;
			}
		}
	}
}
